/* stylelint-disable selector-max-id */

#arve .arve,
#tinymce .arve {
  display: block;
  width: 100%;
  margin-bottom: 1.5em;
}

#arve .arve::after,
#tinymce .arve::after {
  display: table;
  clear: both;
  content: "";
}

#arve .arve.alignleft,
#tinymce .arve.alignleft {
  margin-top: .4em;
  margin-right: 1.5em;
}

#arve .arve.alignright,
#tinymce .arve.alignright {
  margin-top: .4em;
  margin-left: 1.5em;
}

#arve .arve-embed--has-aspect-ratio,
#tinymce .arve-embed--has-aspect-ratio {
  position: relative;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  overflow: hidden;
}

#arve .arve-embed--has-aspect-ratio::before,
#tinymce .arve-embed--has-aspect-ratio::before {
  display: block;
  padding-top: 56.25%;
  content: "";
}

#arve .arve-thumbnail,
#arve .arve-play-btn,
#arve .arve-iframe,
#tinymce .arve-thumbnail,
#tinymce .arve-play-btn,
#tinymce .arve-iframe {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  border: 0;
}

#arve .arve-video,
#tinymce .arve-video {
  width: 100%;
  padding: 0;
  margin: 0;
}

#arve .arve-promote-link,
#tinymce .arve-promote-link {
  float: right;
  font-family: "Open Sans", "Sagoe UI", Arvo, Lato, arial, sans-serif;
  font-size: .8em;
}

.arve-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}




